.cart-container {
  display: flex;
  justify-content: center;

  .cart-content {
    padding: 1em 0;
    max-width: 1170px;

    .cart-header {
      font-size: 24px;
      font-weight: 500;
      // text-align: center;
      // margin: 25px 20px;
    }
    .custom-badge {
      padding: 5px 9px;
    }

    .cart-table {
      border-collapse: collapse;
      border: 1px solid gray;
      width: 100%;
      margin-top: 18px;
      box-shadow: 0px 0px 5px #888888;

      //align-content: center;
      // white-space: nowrap;
      tr,
      td,
      th {
        border: 1px solid gray;
        height: 23px;
        font-size: 87%;
        padding: 6px 10px;

        .cartTableHeader {
          text-align: left;
          //   font-size: 13px;
          color: #3d2277;
          font-weight: 500;
          font-size: 13px;
          padding-left: 12px;
          padding-right: 12px;
          background: #d7dff4;
        }
      }
      tr:nth-child(even) {
        background-color: whitesmoke;
      }
      tr:hover {
        background-color: #e1e1e1;
      }
    }
    .row {
      display: flex;
      margin-top: 20px;
      .form-content {
        width: 50%;
        display: flex;
        flex-direction: column;
        margin: 10px;
        // font-size: 15px;
        margin-right: 0px;
        background-color: #fafafa;
        box-shadow: 0px 0px 5px #888888;
        .label-name {
          font-weight: 500;
          font-size: 13px;
          color: #3d2277;
        }
        .custom-label {
          font-size: 13px;
          padding: 0px 5px;
        }
        .product-container {
          padding: 15px 20px;
          font-size: 14px;
          .custom-margin {
            margin-left: 70px;
          }
          // .custom-label {
          //   font-size: 13px;
          //   padding: 0px 5px;
          // }
          .product-name {
            display: flex;
            align-items: baseline;
            padding-bottom: 10px;
          }
          .sku-container {
            display: flex;
            align-items: center;
            padding-bottom: 15px;
            .topper-margin {
              margin-left: 53px;
            }
            .bed-type-margin {
              margin-left: 75px;
            }
            .bedsheet-margin,
            .pillow-margin {
              margin-left: 43px;
            }
            .cover-margin {
              margin-left: 35px;
            }
            .item-sku {
              width: 100%;
              //font-size: 13px;
              margin-left: 41px;
            }
            .input-dimension {
              width: 100%;
              margin-left: 48px;
              height: 22px;
            }
          }
          .input-error {
            display: flex;
            justify-content: flex-end;
            margin-top: -8px;
          }
          .quantity {
            display: flex;
            padding-bottom: 15px;
          }
          .protector-color {
            display: flex;
            align-items: center;
            .protector-margin {
              margin-left: 46px;
            }
          }
          .extra-specification-container {
            display: flex;
            padding-bottom: 15px;
            .extra-specification-details {
              display: grid;
              width: 100%;
              margin-left: 25px;
              grid-template-columns: 1fr 1fr;
              .custom-check {
              }
            }
          }
        }
        .customer-details {
          font-size: 0.9em;
          padding: 8px 20px;
          line-height: 19px;
          // bottom: 105px;
          // position: absolute;
          .location {
            width: 450px;
          }
          .product-check {
            float: right;
            margin-top: -35px;
            padding-bottom: 10px;
            .check-btn {
              padding: 10px;
              width: 190px;
            }
          }
        }
        label,
        input,
        textarea {
          font-size: 12px;
        }
        // .detail {
        //   margin-left: 0px;
        //   margin-right: 10px;
        // }
        .payment-header {
          padding: 10px 15px;
          height: 20px;
          font-weight: 600;
          color: #3d2277;
          background-color: #d7dff4;
          font-size: 14px;
          // text-transform: uppercase;
        }
        // .left-side {
        //   width: 45%;
        //   float: left;
        // }
        // .right-side {
        //   width: 45%;
        //   float: right;
        //   margin-right: 10px;
        // }

        // 2nd column

        .addcontent {
          padding: 7px 20px;
          .note {
            font-size: 12px;
            font-weight: 700;
            color: red;
          }
          .payment {
            // margin-bottom: -8px;
            font-size: 14px;

            .input-field {
              margin-top: 8px;
              padding: 0px 10px;
              height: 34px !important;
              width: 240px;
            }
          }
          .reason {
            margin-top: 7px;
            // margin-bottom: -5px !important;
            // width: 100%;
            .reason-dropdown {
              width: 100% !important;
            }
          }
          // .custom-label {
          //   font-size: 13px;
          //   padding: 2px 5px;
          // }
          .payment-type {
            font-size: 14px;
            margin-bottom: 7px;
            .payment-type-error {
              display: inline-block;
              padding-left: 10px;
            }
          }
          .paymentMode {
            margin-left: 150px;
            display: flex;
          }
        }
        .checker {
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
        .search-results {
          display: flex;
          // align-items: center;
          margin-bottom: 5px;
          justify-content: space-between;
          .custom-dropdown {
            margin: 10px 0px 0px 0px;
            width: 230px;
          }
          input {
            width: 260px;
          }
          .payment-padding {
            margin-top: 3px;
          }
        }
        .createAccount {
          float: right;
          margin-top: 20px;
          margin-bottom: 5px;
          .button {
            width: 200px;
            padding: 10px;
          }
        }
      }
    }
    .detail {
      margin-left: 0px !important;
      margin-right: 10px !important;
      line-height: 20px;
    }
  }
}
